.App {
    min-height: 450px;
    height: 100vh;
    display: grid;
    padding: 20px;
    min-width: 320px;
    grid-template-columns: 250px 1fr;
    grid-template-rows: 90px minmax(300px, 1fr);
    grid-template-areas:
        'side head'
        'side body';
}



@media (max-width: 720px) {
    .App {
        min-height: 500px;
        grid-template-columns: minmax(275px, 1fr);
        grid-template-rows: 120px 50px 1fr;
        grid-template-areas:
            'side'
            'head'
            'body';
    }
}